import React, { useState, useEffect } from "react";
import { Form, ProgressBar, Alert } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import {
  feedformulationlist,
  feedformulationdelete,
} from "../../reduxx/actions/actions";

function ListFormulation() {
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(feedformulationlist());
  }, []);
  const feedlist = useSelector(
    ({ feedformulationlist }) => feedformulationlist.payload
  );
  return (
    <div>
      <div className="card">
        <div className="card-body">
          <h4 className="card-title">Feed Formulation</h4>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <p className="card-description mt-3"> List </p>
            <Link
              to="/feed/formulation/add"
              className="btn btn-primary"
              style={{ display: "flex", alignItems: "center" }}
            >
              Add
            </Link>
          </div>
          <div className="table-responsive">
            <table className="table">
              <thead>
                <tr>
                  <th> S No </th>
                  <th> Type</th>
                  <th> Ingredients </th>
                  <th> Range</th>
                  <th> CP </th>
                  <th> ME </th>

                  <th> Actions </th>
                </tr>
              </thead>
              <tbody>
                {Array.isArray(feedlist) ? (
                  feedlist.map((link, i) => {
                    return (
                      <tr>
                        <td> {i + 1}</td>
                        <td> {link.type} </td>
                        <td> {link.ingredients} </td>
                        <td>
                          {link.range.from} - {link.range.to}
                        </td>
                        <td> {link.cp} </td>
                        <td> {link.me} </td>
                        {
                          // status badge
                        }

                        <td>
                          <span
                            className="badge badge-danger"
                            style={{ cursor: "pointer" }}
                            onClick={() =>
                              dispatch(
                                feedformulationdelete({ id: link.id + "" })
                              )
                            }
                          >
                            Delete
                          </span>
                        </td>
                      </tr>
                    );
                  })
                ) : (
                  <tr>
                    <td
                      colSpan={7}
                      style={{ textAlign: "center", fontWeight: "bold" }}
                    >
                      <Alert className="p-3" variant="danger">
                        No Data Found
                      </Alert>
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ListFormulation;
